.card {
  display: grid;
  grid-template-rows: 2fr 1fr;
  height: 100%;
  gap: 1em;
}

.answers {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 0.5em;
}

.answers {
  --color-one: #e11b3e;
  --color-one-alt: #cb1838;
  --color-two: #1467cf;
  --color-two-alt: #125dba;
  --color-three: #d69e01;
  --color-three-alt: #c18e01;
  --color-four: #28880d;
  --color-four-alt: #247a0c;
}

.answers button {
  border-radius: 0.4em;
  color: white;
  font-size: 1.1em;
  font-weight: 700;
}

.answers button:nth-child(1) {
  background-color: var(--color-one);
}

.answers button:nth-child(1):hover {
  background-color: var(--color-one-alt);
}

.answers button:nth-child(2) {
  background-color: var(--color-two);
}

.answers button:nth-child(2):hover {
  background-color: var(--color-two-alt);
}

.answers button:nth-child(3) {
  background-color: var(--color-three);
}

.answers button:nth-child(3):hover {
  background-color: var(--color-three-alt);
}

.answers button:nth-child(4) {
  background-color: var(--color-four);
}

.answers button:nth-child(4):hover {
  background-color: var(--color-four-alt);
}



